import { CommonModule } from '@angular/common';
import { FormsModule } from "@angular/forms";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { MetaDemoRoutingModule } from "./meta-select-demo.routing.module";
import { MetaSelectDemoComponent } from "./meta-select-demo.component";
import { MetaDemoBasicComponent } from "./meta-select-demo-basic.component";
import { CustomComponentsModule } from "../../../custom-components/custom-components.module";



@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    NzGridModule,
    NzToolTipModule,
    NzCodeBoxModule,
    MetaDemoRoutingModule,
    CustomComponentsModule
  ],
  declarations: [
    MetaSelectDemoComponent,
    MetaDemoBasicComponent
  ]
})
export class MetaDemoModule { }



