import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'meta-demo-basic',
  template: `

    <h3 style="margin-bottom: 30px">字典组件可以通过filterOptions数组来过滤不想要的选项</h3>
    <div>
      <div style="float: left">
        <h3 style="margin-bottom: 10px">字典组件--多选</h3>
        <p style="margin-bottom: 10px">multiSelect="true" 属性控制多选</p>
        <meta-select width="150px" multiSelect="true" defaultLabel="请选择…" [(ngModel)]="metaCode1"
                     type="riskReason" [filterOptions]="filterOptions"></meta-select>
        <span style="margin-left: 25px">{{metaCode1}}</span>
        <br>
        <button style="margin-top: 10px" (click)="doTest2()">测试取字典Code</button>
      </div>
      <div style="float: left;margin: 0 140px">
        <h3 style="margin-bottom: 41px">字典组件--单选</h3>
        <meta-select width="150px" defaultLabel="请选择…" [(ngModel)]="metaCode2" type="abnormalDuty"></meta-select>
        <span style="margin-left: 25px">{{metaCode2}}</span>
        <br>
        <button style="margin-top: 10px" (click)="doTest22()">测试取字典Code</button>
      </div>
      <div style="float: left">
        <h3 style="margin-bottom: 22px">字典组件-带onChange等事件</h3><br>
        <meta-select width="150px" defaultLabel="请选择…"
                     [(ngModel)]="metaCode2"
                     (onChange)="onChangeHandler($event)"
                     (onFocus)="onFocusHandler($event)"
                     (onBlur)="onBlurHandler($event)"
                     type="repair"></meta-select>
        <span style="margin-left: 25px">{{metaCode2}}</span>
        <br>
        <button  style="margin-top: 10px" (click)="doTest22()">测试取字典Code</button>
      </div>
    </div>
    <div class="content-right" style="margin-top: 215px">
      <h3>字典类型type</h3>
      <ul>
        <li>service-服务类型</li>
        <li> repair-维修类型</li>
        <li>
          fhreturn-返货类型
        </li>
        <li>
          riskReason-出险原因
        </li>
        <li>
          specifyLogistics-指定物流
        </li>
        <li>
          abnormalDuty-异常责任方
        </li>
        <li>
          complement-补件-任务类型
        </li>
        <li>
          transportCompany-物流公司
        </li>
        <li>
          goodsunit-商品单位
        </li>
        <li>
          abnormalHandleWay-异常处理方式
        </li>
      </ul>
    </div>
  `,
  styles: []
})
export class MetaDemoBasicComponent implements OnInit {
  areaCode: String = "110106000000";
  metaCode1: String;
  metaCode2: String = "serviceProvider";
  //过滤字典
  filterOptions: any[] = ['逗比'];

  constructor() {

  }

  ngOnInit() {
  }

  doTest(): any {
    alert(this.areaCode);
  }

  doTest2(): any {
    alert(this.metaCode1);
  }

  doTest22(): any {
    alert(this.metaCode2);
  }

  onChangeHandler(event) {
    //console.info('onChangeHandler',event)
    // alert('你选中了：'+event.value);

  }

  onFocusHandler(event) {
    //console.info('onFocusHandler')
  }

  onBlurHandler(event) {
    //console.info('onBlurHandler')
  }
}



